
.container{
    padding: 20px 0 30px 0;
    margin-bottom: 30px;
    font-family: "Titillium Web", sans-serif;
    font-weight: 200;
    width: 100%;
    display: flex;
    flex-direction: column;
    transition: all 0.5s ease;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background-color: rgba(255, 254, 254, 0.932);
    
  
}
.container.dark{
    transition: all 0.5s ease;
    background-color: rgb(110, 107, 107);
}
.container .action-head h2{
   text-align: left;
   margin-left: 40px;
   margin-bottom: 30px;
   font-size: 24px;
}
.container .action-head a{
    text-decoration: underline;
}
.container.dark .action-head h2{
    color: white;
   
}
.container .action-head a{
    color: rgb(0, 41, 155);
   
}

.box{
    padding: 3px;
    margin: 4px;
    border-radius: 6px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

/*Responsive*/
@media(max-width:773px){
    .container .action-head h2{
        font-size: 16px;
    }
}
@media(max-width:525px){
   .container .action-head h2{
        font-size: 18px;
    }
    .box{
        gap: 5px;
    }
}
@media(max-width:452px){
    .container .action-head h2{
        font-size: 16px;
        text-align: start;
    }
    .box{
        gap: 10px 20px;
    }
}
@media(max-width:425px){
    .container .action-head{
        display: flex;
       margin-left: -20px;
    }
    .container .action-head h2{
        font-size: 15px;
        text-align: start;
    }
    .box{
        gap: 10px 15px;
    }
}



