
header{
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    z-index: 99;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 13%;
    color: white;
    backdrop-filter: blur(20px);
    
}
.logo{
    font-size: 35px;
    color: blue;
    text-shadow: 0px 0px 3px rgba(0,0,0,0.6);
}
.logo span{
    color: rgb(252, 105, 8);
    font-weight: 700;
}
.navlist{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 6px 10px;
}
.navlist li{
    position: relative;
    margin: 0 20px;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.5s;
}
.navlist .link{
    color: rgb(252, 105, 8);
    font-weight: 700;
}
.navlist li .link::after{
    content: " ";
    top: 35px;
    left: 0;
    position: absolute;
    z-index: 1000;
    height: 3px;
    border-radius: 9px;
    width: 100%;
    background-color:rgb(252, 105, 8);
    transition: all 0.5s ease ;
    transform: scale(0);
}
.navlist li .link:hover::after{
    transition: all 0.5s ease-in-out ;
    transform: scale(1);
}


.nav-btn{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.moon,.sun{
    font-size: 32px;
    color: rgb(240, 2, 208);
    margin-right: 35px;
    cursor: pointer;
}
.sun{
    color: rgb(252, 105, 8);
}
.nav{
    padding: 10px 25px;
    font-size: 22px;
    border-radius: 7px;
    border: none;
    outline: none;
    background: transparent;
    border: 3px solid rgb(252, 105, 8);
    color:rgb(252, 105, 8);
    cursor: pointer;
    transition: all 0.5s;
}
.nav:hover{
   background-color: rgb(252, 105, 8);
   color: white;
    
}
#menu-icon{
    margin-left: 20px;
    font-size: 30px;
    display: none;
    color: rgb(252, 105, 8);
}

/*Responsive*/
@media(max-width:1167px){
.logo{
  margin-left: -75px;
}
}
@media(max-width:1079px){
    .logo{
        font-size: 22px;
    }
    .nav{
        padding: 8px 12px;
        font-size: 18px;
    }
    .navlist li{
        font-size: 16px;
    }
}
@media(max-width:877px){
    .sun,.moon{
        font-size: 26px;
    }
    .navlist{
       padding: 2px 4px;
       
    }
    .navlist li{
        font-size: 15px;
    }
    .nav{
        font-size: 15px;
    }
}
@media(max-width:815px){
    /* .navlist.inActive{
        top: 58px;
        right: 0; 
    } */
     .navlist.isActive{
        position: fixed;
        display: block;
        top: 58px;
        right: 0;
        height: 100vh;
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        transform: translateX(0%);
        transition: all 0.4s ease-in-out;
     }
     .navlist li{
        padding: 20px 0;
        transition: all 0.4s ease-in-out
     }
     .navlist{
        position: fixed;
        background-color:rgb(126, 201, 231);
        top: 58px;
        right: 0;
        height: 100vh;
        width: 100%; 
        z-index: 999;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        transform: translateX(100%);
        transition: all 0.4s ease-in-out
     }
     .dark .navlist{
        background-color: rgb(6, 6, 53);
     }
     .navlist li .link::after{
        top: 48px;
     }
    #menu-icon{
        display: block;
        margin-right: -80px;
    }
}
@media(max-width:615px){
    .logo{
        margin-left: 5px;
    }
    #menu-icon{
        margin-right: -40px;
    }
}
@media(max-width:525px){
    .logo{
        font-size: 26px;
      margin-left: -40px;
    }
    
}
@media(max-width:475px){
   header{
  height: 62px;
   }
   .navlist.isActive{
top: 62px;
right: 0;
   }
   .navlist{
    top: 62px;
   }
    .nav{
        padding: 8px 10px;
    }
    .sun,.moon{
        font-size: 30px;
        margin-right: 10px;
    }
    
}
@media(max-width:393px){
    .logo{
        font-size: 22px;
        margin-left: -25px;
    }
.sun,.moon{
    font-size: 28px;
   
}
        .nav{
            padding: 6px 8px;
            margin-right:-10px;
        }
       
    

    #menu-icon{
        font-size: 28px;
        margin-right: -35px;
    }
}

