
  .wrapper{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    /* background-color: rgb(138, 219, 247); */
    background-image: url("../../Assets/image.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
 .wrapper .shareclose{
 position: fixed;
 top: 80px;
 right: 20px;
 font-size: 30px;
 color: rgb(71, 226, 9);
 cursor: pointer;
 }

  .form-container{
    background-color: aliceblue;
    width: 350px;
    height: 400px;
    position: absolute;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  }
  .wrapper.dark .form-container{
    background-color: rgb(15, 15, 46);
    color: white;
  }
  .form-container.sign-in{
    transform: rotate(7deg);
  }

  .form-container  h2{
    font-size: 30px;
    text-align: center;
    margin-top: 10px;
    color: rgb(252, 54, 19);
  }

  .form-container  .form-group{
    position: relative;
    width: 350px;
    height: 50px;
    margin: 25px 0;
   
  }
  .form-container  .form-group input{
    height: 100%;
    width: 90%;
    margin: 0 20px;
    padding: 0 10px 0 15px;
    background: transparent;
    border: none;
    outline: none;
    border-bottom: 2px solid tomato;
  }
  .wrapper.dark .form-container  .form-group input{
    color: white;
  }
  .form-container .form-group label{
    position: absolute;
    top: 50%;
    margin: 0 20px;
    left: 0;
    transform: translateY(-160%);
    font-size: 16px;
    pointer-events: none;
    transition: all 0.5s ease;
  }
  form .form-group input:focus~label,
  form .form-group input:valid~label{
   top: -5px;
  }
  .form-container  .form-group .login-icon{
    position: absolute;
    top: 50%;
    left: 0;
    color: rgb(245, 45, 10);
    margin: 0 15px;
    transform: translateY(-50%);
  }
  .form-container .forget-pass:hover{
    text-decoration: underline;
    cursor: pointer;
  }
  .form-container  .btn{
    position: relative;
    width: 90%;
    height: 45px;
    margin: 0 20px;
    font-weight: bold;
    font-size: 18px;
    outline: none;
    border: none;
    border-radius: 6px;
   background-color:  tomato;
    color: rgb(255, 255, 255);
    cursor: pointer;
    transition: all 0.5s ease;
  }
  .form-container .btn:hover{
    background-color: rgb(240, 40, 5);
  }
  .forget-pass{
   margin: -20px 0 15px 20px;
  }
  .form-container  .link{
    font-size: 14px;
    text-align: center;
    margin-top: 8px;
  }
  
  .form-container  .link p a{
    color: blue;
    text-decoration: none;
    font-weight: bold;
    
  }
  .form-container  .link p a:hover{
    text-decoration: underline;
    cursor: pointer;
  }
  /*Animation*/

  .wrapper.animated-signin .form-container.sign-up{
    animation: signup-page 1s ease-in-out forwards;
  }

  @keyframes signup-page{
    0%{
      transform: translateX(0);
    }
    50%{
      transform: translateX(25rem);
    }
    100%{
      transform: translateX(0) rotate(7deg);
      z-index: 0;
    }
  }
  .wrapper.animated-signin .form-container.sign-in{
    animation: signin-page 1s ease-in-out forwards;
  }
  @keyframes signin-page{
    0%{
      /* transform: translateX(-30px) rotate(7deg); */
      transform: translateX(0);
    }
    50%{
      transform: translateX(-40px) rotate(7deg) scale(1.1); 
    }
    100%{
      transition-delay: 1s;
      transform: translateX(0) rotate(0) scale(1);
      z-index: 1;
    }
  }

  /*second portion*/
  .wrapper.animated-signup .form-container.sign-in{
    animation: signin-page-rotation 1s ease-in-out forwards;
  }

  @keyframes signin-page-rotation{
    0%{
      transform: translateX(0);
      z-index: 1;
    }
    50%{
      transform: translateX(-25rem);
      
    }
    100%{
      transform: translateX(0) rotate(7deg);
      z-index: 0;
    }
  }
  .wrapper.animated-signup .form-container.sign-up{
    animation: signup-page-rotation 1s ease-in-out forwards;
  }
  @keyframes signup-page-rotation{
    0%{
     
      transform: translateX(0);
    }
    50%{
      transform: translateX(40px) rotate(7deg) scale(1.1); 
    }
    100%{
      transition-delay: 1s;
      transform: translateX(0) rotate(0) scale(1);
      z-index: 1;
    }
  }

  /*Responsive*/

  @media(max-width:425px){
    .wrapper .shareclose{
      top: 75px;
      right: 5px;

      }
    .form-container{
      width: 300px;
      height: 380px;
      
    }
    .form-container  h2{
      font-size: 24px;
  
    }
  
    .form-container  .form-group{
      width: 250px;
      height: 45px;
      margin: 25px 0;
     
    }
    .form-container .form-group input{
      height: 100%;
      width: 90%;
      margin: 0 30px;
      padding: 0 10px 0 15px;
    }
  }

  @media(max-width:360px){
    .form-container{
      width: 260px;
      height: 380px;
      
    }
    .form-container  h2{
      font-size: 22px;
  
    }
  
    .form-container  .form-group{
      width: 250px;
      height: 45px;
      margin: 25px 0;
     
    }
    .form-container .form-group input{
      height: 100%;
      width: 90%;
      margin: 0 20px;
      padding: 0 10px 0 15px;
    }
    .form-container .form-group label,.link p{
      font-size: 12px;
    }
    .form-container  .form-group .login-icon{
      margin: 0 10px;
      font-size: 12px;
      transform: translateY(-50%);
    }
    .form-container  .btn{
      position: relative;
      width: 80%;
      height: 40px;
      
    }
  }
