.movie-display{
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    padding: 80px 0 50px 0;
    background-color: rgb(240, 240, 240);
}
.movie-display.dark{
    background-color: rgba(10, 12, 27, 0.863);
    color: white;
}
.movie-display-box1{
    background-color: rgb(144, 188, 245);
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-top: 30px;
    flex-direction: column;
}
.movie-galary{
    display: flex;
    align-items: center;
    justify-content: space-around;
   /* background-color: blue; */
    width: 450px;
}
.movie-display-box1 .movie-galary img{
    height: auto;
    width: 300px;
}

.share-option{
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 26px;
    color: rgb(245, 61, 92);
    width: 320px;
}
.share-option .share-box{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    border: 3px solid rgb(245, 61, 92);
    transition: all 0.5s ease-in-out;
}
.share-option .share-box{
    transform: scale(1) translateY(0px);
    transition: all 0.5s ease-in-out;
    
}
.share-option .share-box:hover{
    transform: scale(1.2) translateY(-5px);
    box-shadow: 0 0 25px rgb(245, 61, 92);
    transition: all 0.5s ease-in-out;
    background-color: rgb(245, 61, 92);
    
   
}
.share-option .share-box{
    margin: 20px 0;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}
.share-option .share{
    color:rgb(245, 61, 92);
    transition: all 0.5s ease-in-out;
}
.share-option .share:hover{
    color: white;
    transform: scale(1.2) translateY(-5px) rotate(20deg);
    transition: all 0.5s ease-in-out;
}
.share-option .favorite{
    cursor: pointer;
    color: rgb(245, 61, 92);
    font-size: 30px;
}
.share-option .add-favourite{
    cursor: pointer;
    font-size: 30px;
    color:rgb(245, 61, 92) ;
}
.movie-display-box1.dark{
    background-color: rgba(61, 61, 61, 0.863);
}
.movie-display-box1.dark .movie-head{
    color: white;
}
.movie-head{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.movie-display-box1 h1{
padding-bottom: 20px;
}



.play-button button{
    padding: 15px 25px;
    border-radius: 6px;
    background-color: rgb(255, 105, 46);
    color: white;
    outline: none;
    border: none;
    font-size: 15px;
    font-weight: bold;
    margin: 30px ;
    border: 3px solid rgb(255, 105, 46);
    cursor: pointer;
    transition: all 0.55s ease;
}
.play-button .playnow{
background: transparent;
}
.play-button .trailar{
    color: white;
}
.play-button .playnow:hover{
    background: rgb(255, 105, 46);
    color: white;
    transition: all 0.55s ease;
    }
    
    .play-button .trailar:hover{
        background: transparent;
        color: white;
        transition: all 0.55s ease;
    }


    .movie-display-box2{
        display: flex;
        flex: 1;
        justify-content: center;
        align-items: center;
       /* background-color: blue; */
       padding: 20px 0;
        
    }
    .movie-display-box2-innerbox
    {
        display: flex;
        flex: 1;
        margin: 0 30px;
        display: flex;
        flex-direction: column;
        gap: 40px 0;
    }
    .movie-display-about-movie,
    .movie-display-all-description,
    .striming{
        width: 100%;
        background-color: rgb(255, 255, 255);
        border-radius: 10px;
        padding: 20px 20px 30px 10px;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    }
    .movie-display-box2.dark .movie-display-about-movie,
    .movie-display-box2.dark .movie-display-about-movie,
    .movie-display-box2.dark .movie-display-all-description,
    .movie-display-box2.dark .striming{
        background-color: rgba(80, 77, 77, 0.863);
    }
    .movie-display-box2 .movie-display-about-movie h1,
    .movie-display-box2 .movie-display-all-description h1,
    .movie-display-box2 .striming h1{
       margin-bottom: 10px;
    }
    .movie-display-all-description-content li{
        padding: 3px 0;
    }
.strimg-app{
    width: 200px;
    padding: 20px 0;
    display: flex;
    justify-content: space-around;
}
    .st-box{
        height: 50px;
        width: 50px;
        margin-right: 20px;
        transform: translateY(5px);
        transition: all 0.5s ease;
        cursor: pointer;
    }
    .st-box img{
        height: 100%;
        width: 100%;
        border-radius: 50%;
      
    }
    .st-box:hover{
        transform: translateY(-5px);
        transition: all 0.5s ease;
    }


/*Responsive*/

@media(max-width:1027px){
    .play-button button{
        padding: 15px 18px;
        margin: 30px 15px;
    }
}
@media(max-width:862px){
    .movie-display-about-movie h1,
    .movie-display-all-description-head h1,
    .striming h1{
        font-size: 26px;
    }
    .movie-display-about-movie p,
    .movie-display-all-description-content li{
        font-size: 15px;
    }
}

@media(max-width:812px){
    .movie-display-about-movie h1,
    .movie-display-all-description-head h1,
    .striming h1{
        font-size: 22px;
    }
}
@media(max-width:768px){
    .movie-display{
        display: flex;
        flex-direction: column;
    }
    .movie-display-box1 .movie-galary{
        height:350px ;
        width: 300px;
        /* background-color: aqua; */
    }
    .movie-display-box1 .movie-galary img{
        width: 100%;
        height: 100%;
    }
}
@media(max-width:393px){
    .movie-display-about-movie p,
    .movie-display-all-description-content li
    {
        font-size: 15px;
    }

    .movie-display-box1 .movie-galary{
        height:320px ;
        width: 280px;
        /* background-color: aqua; */
    }
    .movie-display-box1 .movie-galary img{
        width: 100%;
        height: 100%;
    }
    .movie-display-about-movie h1,
    .movie-display-all-description-head h1,
    .striming h1{
        font-size: 19px;
    }
    
}




















