.footer-box1 {
    /* background-color: red; */
    /* display: grid; */
    /* grid-template-columns: 1.5fr 1fr 1fr 1fr; */
    /* justify-items: center; */
    display: flex;
    padding-top: 10px;
    background-color: rgb(30, 4, 71);
    color: white;
   
}
.footer-box1.dark{
    background-color: rgb(22, 30, 43);
}
.footer-section1{
    display: flex;
    flex-direction: column;
    padding: 0 10px 0 15px;
    flex: 1.3;
}
.footer-section2,
.footer-section3,
.footer-section4{
    display: flex;
    flex-direction: column;
    flex: 1;
    padding-left: 60px;
}

.footer-logo{
    display: flex;
    font-size: 22px;
}
.movielogo{
    display: flex;
    font-size: 30px;
    margin-top: 10px;
}
.footer-logo span{
    color: rgb(255, 68, 0);
}
.footer-content{
    font-size:15px ;
    padding: 10px 0;
}

.footer-socialmedia{
    display: flex;
    flex-direction: column;
}
.social-container{
    display: flex;
    margin: 10px 0;
}

.social-container{
    display: flex;
    padding: 10px 0;
}
.social-box{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    width: 35px;
    
    color: rgb(252, 75, 5);
    border: 2px solid rgb(255, 72, 0);
    margin: 0px 15px;
    border-radius: 50%;
    transform: translateY(0px);
    transition: all 0.5s ease;
    cursor: pointer;
}
.social-box .sicon{
    font-size: 22px;
}
.social-box:hover{
    color: white;
    transform: scale(1.2) translateY(-10px);
    box-shadow: 0 0 25px rgb(255, 74, 3);
    background-color: rgb(255, 72, 0);
}
.footer-section2 li,
.footer-section3 li,
.footer-section4 li{
    font-size: 15px;
    text-align: left;
    padding: 4px 0;
    cursor: pointer;
    transition: all 0.5s ease;
}
.footer-section2 li:hover,
.footer-section3 li:hover,
.footer-section4 li:hover{
transform: translateX(-5px);
color: orangered;
transition: all 0.5s ease;
}
.footer-box2 p{
    font-size: 15px;
    text-align: center;
}
.footer-box2{
    background-color: rgb(2, 2, 44);
    color: white;
    padding: 15px;
}
.footer-box2.dark{
    background-color: rgb(28, 43, 66);
}

/*Responsive*/
@media(max-width:878px){
    .footer-section2 li h2,
    .footer-section3 li h2,
    .footer-section4 li h2{
     font-size: 20px;
    }
}
@media(max-width:768px){
    /* .footer-box1{
        display: grid;
        grid-template-columns: 1.5fr 1fr;
        grid-template-rows: 1fr 1fr;
       justify-items: flex-start;
    } */
    .footer-section2,
    .footer-section3,
    .footer-section4{
     padding: 10px 0 10px 15px;
    }
}

@media(max-width:612px){
    .footer-box1{
        flex-wrap: wrap;
    }
    .footer-section2,
    .footer-section3,
    .footer-section4{
     padding:0 35px;
    }
}
@media(max-width:525px){
    .footer-logo h2{
        font-size: 25px;
    }
    .footer-logo .movielogo{
        font-size: 25px;
        margin: 6px;
    }
    .footer-section2 li h2,
    .footer-section3 li h2,
    .footer-section4 li h2{
     font-size: 16px;
    }
    .footer-content,
    .footer-section2 li,
    .footer-section3 li,
    .footer-section4 li{
    font-size: 12px;
   
}
.social-box{
    height: 25px;
    width: 25px;
    margin: 0px 10px;
}
.social-box .sicon{
    font-size: 15px;
}
.footer-box2 p{
    font-size: 12px;
    text-align: center;
}
}
@media(max-width:360px){
    /* .footer-box1{
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 0.5fr 0.5fr;
    } */
    .footer-logo h2{
        font-size: 18px;
    }
    .footer-logo .movielogo{
        margin-top: 2px;
    }
    /* .footer-section1,
    .footer-section2,
    .footer-section3,
    .footer-section4{
        padding: 20px 8px 0 8px;
    } */
.footer-box2 p{
    font-size: 10px;
}
}