
/*className="bg-white text-indigo-600 hover:bg-indigo-100"*/

.container-button{
    background-color: black;
      color: white;
  }
  
  /* className="fixed inset-0 bg-black bg-opacity-50 backdrop-blur-sm flex items-center justify-center p-4" */
  
  .model-container{
      position: fixed;
      display: flex;
      align-items: center;
      justify-content: center;
      /* background-color: wheat; */
      /* backdrop-filter: blur(20px); */
      /* background-color: rgb(235, 233, 233) transparent; */
      backdrop-filter: blur(20px);
      inset: 0;
      z-index: 9999;
  }
  
  /* className="bg-white rounded-lg shadow-2xl p-6 w-full max-w-md relative" */
  
  .inner-box{
  background: rgb(114, 236, 195);
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  position: relative;
  /* width: 400px;
  height: 250px; */
  width: 400px;
  padding: 20px;
  }
  
  /*className="absolute top-2 right-2 text-gray-500 hover:text-gray-700"*/
  .inner-button{
      background: transparent;
      border: none;
      outline: none;
      position: absolute;
      right: 0;
      top: 0;
      padding-top: 5px;
      /* top: 5px  ; */
  }
  .inner-button:hover{
      background-color: red;
      color: white;
  }
  
  /*className="bg-indigo-600 text-white hover:bg-indigo-700"*/
  
  .inner-close-button{
      padding: 10px 20px;
      background-color: blue;
      color: white;
      border-radius: 6px;
      outline: none;
      border: none;
  }
  .inner-close-button:hover{
      background-color: rgb(3, 3, 197);
  }
  
  .share-social{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 20px;
  }

  /*Responsive*/
  @media(max-width:425px){
    .inner-box{
        background: rgb(114, 236, 195);
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        position: relative;
        height: 220px;
        width: 340px;
        padding: 20px;
        }
  }
  @media(max-width:360px){
    .inner-box{
        height: 200px;
        width: 300px;
        padding: 20px;
        }
  }