
.container_box{
    padding: 80px 0 50px 0;
    /* margin-bottom: 30px; */
    font-family: "Titillium Web", sans-serif;
    font-weight: 200;
    width: 100%;
    display: flex;
    flex-direction: column;
    transition: all 0.5s ease;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background-color: rgba(255, 255, 255, 0.932);
    
  
}
.container_box.dark{
    transition: all 0.5s ease;
    background-color: rgb(110, 107, 107);
}
.container_box .action-head h2{
   text-align: left;
   margin-top: 20px;
   margin-left: 40px;
   margin-bottom: 20px;
   font-size: 24px;
}
.container_box .action-head a{
    text-decoration: underline;
}
.container_box.dark .action-head h2{
    color: white;
   
}
.container_box.dark .action-head a{
    color: rgb(7, 118, 245);
   
}
/* .action-image-box{
    display: flex;
  background-color: blue;
  height: 100vh;
  flex-wrap: wrap;
} */
.box{
    padding: 3px;
    margin: 4px;
    border-radius: 6px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

/*Responsive*/
@media(max-width:773px){
    .container_box .action-head h2{
        font-size: 16px;
    }
}
@media(max-width:525px){
   .container_box .action-head h2{
        font-size: 22px;
    }
    .box{
        gap: 5px;
    }
}
@media(max-width:452px){
    .container_box .action-head h2{
        font-size: 12px;
        text-align: start;
        
    }
    .box{
        gap: 10px 20px;
    }
}
@media(max-width:425px){
    .container_box .action-head{
        display: flex;
       margin-left: -20px;
    }
    .container_box .action-head h2{
        font-size: 16px;
        text-align: start;
    }
    .box{
        gap: 10px 15px;
    }
}



