.main{
    background-color: rgb(240, 237, 237);
   transition: all 0.5s ease-in-out;
    /* margin-bottom: 40px; */
    padding-bottom: 40px;
   
}
.main.dark{
    transition: all 0.5s ease-in-out;
    background-color: rgb(77, 76, 76);
  
}

.home{
    padding: 80px 13%;
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    align-items: center;
    gap: 3rem;
}
.home-img img{
    width: 320px;
    height: auto;
    }
    .home-text h4{
    color: white;
    font-size: 15px;
    text-transform: capitalize;
    font-weight: 700;
    margin-bottom: 25px;
    }
     .home-text h3{
        color: white;
    font-size: 22px;
    text-transform: capitalize;
    font-weight: 600;
    }
     .home-text span{
        color: rgb(255, 231, 14);
    }
     .home-text h2{
    color: white;
    font-size: 50px;
    text-transform: capitalize;
    font-weight: 700;
    margin-bottom: 15px;
    }
    .home-text p{
        max-width: 480px;
        color: white;
        font-size: 17px;
        font-weight: 400;
        line-height: 30px;
        margin-bottom: 40px;
    }
     .home-btn{
        display: flex;
        align-items: center;
    }
    .home-btn a{
        color: rgb(248, 245, 245);
        font-size: 17px;
        text-transform: uppercase;
        font-weight: 700;
        padding: 10px 24px;
        background: transparent;
        margin-right: 30px;
        border-radius: 10px;
        border: 2px solid rgb(255, 255, 255);
        transition: all 0.5s;
    }
    .home-btn .hm-btn1 .i{
    vertical-align: middle;
    font-size: 15px;
    margin-right: 5px;
    }
    .home-btn a:hover{
      color: rgb(252, 3, 3);
      background-color: white;
    }
    .home-right{
        position: absolute;
        right: 13%;
        top: 40%;
    }
    .home-right a{
        display: block;
        color: white;
        font-size: 26px;
        margin: 20px 0;
        transition: all 0.5s ease;
    }
    .home-right a:hover{
        transform: translateY(-5px);
        color: rgb(252, 5, 5);
    }


    /*Responsive*/

    @media(max-width:917px){
        .home{
            padding: 80px;
            height: 100%;
          
           }
           .home-text{
            margin-top: 40px;
           }
           .home-text h2{
            font-size: 42px;
           }
            .home-btn a{
                font-size: 11px;
            }
            
           .home {
             grid-template-columns: 1fr;
             height: 100%;
           }
           .home-right{
            right: 30px;
            top: 50%;
           }
           .home-img img{
            width: 220px;
           }
    }
    @media(max-width:475px){
        .home{
            padding-left: 30px;
        }
    }
    @media(max-width:394px){
        .home-text h2{
            font-size: 30px;
        }
        .hm-btn1{
            height: 40px;
            width: 103px;
            /* padding-left: -10px; */
        }
        .hm-btn1 .i{
            width: 10px;
            margin-left: -14px;
        }
        
    }