
.movie-box1{
    top: 0;
    left: 0;
    position: absolute;
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
   
  }
  .body-content{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;
  }
  .movie-box1 h1{
    margin-top: 36px;
    color: white;
    font-size: 52px;
  }
  .movie-box1 span{
    color: rgb(252, 105, 8);
  }
  .body-content h2{
    font-size: 40px;
    padding: 20px 0 15px 0;
    text-align: center;
  }
  .body-content p{
    font-size: 20px;
    text-align: center;
  }
.home-btn{
    display: flex;
    justify-content:space-around;
    align-items: center;
    margin-top: 40px;
    padding: 40px 0;
    width: 400px;
}

  .home-btn .hm-btn1{
  padding: 14px 17px;
   border-radius: 6px;
   font-size: 15px;
   border: 3px solid rgb(252, 105, 8);
   outline: none;
   background-color: rgb(252, 105, 8);
   font-weight: bold;
   transition: all 0.55s ease-in;
   cursor: pointer;
  }
  .home-btn .hm-btn1:hover{
    color: rgb(252, 105, 8);
    background: transparent;
    transition: all 0.55s ease-in;
  }
  .home-btn .hm-btn2{
    padding: 14px 30px;
    border-radius: 6px;
    background: transparent;
    border: 3px solid rgb(252, 105, 8);
    color: rgb(252, 105, 8);
    font-size: 15px;
    font-weight: bold;
    transition: all 0.55s ease-in;
    cursor: pointer;
  } 
  .home-btn .hm-btn2:hover{
 background-color: rgb(252, 105, 8);;
 color: white;
 transition: all 0.55s ease-in;
  } 
 


  /*Responsive*/

  @media(max-width:1079px){
    .movie-box1 h1{
      font-size: 45px;
    }
    .body-content h2{
      font-size: 34px;
      align-items: center;
    }
  }
  @media(max-width:865px){
    .movie-box1 h1{
      font-size: 38px;
      /* padding-top:55px ; */
    }
    .movie-box1 p{
      font-size: 15px;
    }
 
  }
  @media(max-width:816px){
    .movie-box1 h1{
      font-size: 37px;
    }
    .body-content h2{
      font-size: 27px;
      align-items: center;
    }
    .body-content p{
      font-size: 14px;
    }
  }
  @media(max-width:670px){
    .body-content p{
      font-size: 12px;
    }
  }
  @media(max-width:575px){
    .movie-box1{
      padding: 0 10px;
    }
    .movie-box1 h1{
      font-size: 32px;
      margin-bottom: 15px;
    }
    .body-content h2{
      font-size: 22px;
      align-items: center;
      margin-bottom: 15px;
    }
    .body-content p{
      font-size:17px ;
    }
  }
  @media(max-width:425px){
    .movie-box1 h1{
      font-size: 29px;
      margin-top: -10px;
    }
    .body-content h2{
      font-size: 22px;
      align-items: center;
    }
    .body-content p{
      font-size: 17px;
      margin-top: 5px;
    }
    .home-btn .hm-btn1{
      padding: 15px 10px;
      font-size: 14px;
     }
     .home-btn .hm-btn2{
      padding: 15px 25px;
     }
  }
  @media(max-width:396px){
    .home-btn{
      width: 320px;
    }
   .home-btn .hm-btn1{
    width: 125px;
    height: 50px;
    margin-left: 14px;
    font-size: 10px;
   }
   .home-btn .hm-btn1 .i{
    margin-left: 5px;
   }
   .home-btn .hm-btn2{
    width: 100px;
    height: 50px;
   font-size: 10px;
   }
   .home-btn .hm-btn1 .i{
    font-size: 5px;
   }

    
  }
  @media(max-width:358px){
   
    .movie-box1 h1{
      font-size: 37px;
      margin-top: 5.5rem;
    }
    .body-content h2{
      font-size: 20px;
      margin-top: 6px;
      align-items: center;
    }
    .body-content p{
      font-size: 15px;
    } 
  }

  @media(max-width:325px){
    .movie-box1 h1{
      font-size: 27px;
    }
  }