.sponser-box{
    text-align: center;
    /* margin: 20px 0; */
    padding-top: 10px;
    
}
.sponser-box.dark{

    background-color: rgb(36, 44, 56);
    color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.sponser-box h1{
    padding: 10px 0;
}
.mar{
   height: 160px;
}
.box-sp{
    height:100px ;
    width: 150px;
    margin: 0 40px;
}
.box-sp img{
    aspect-ratio: 3/2;
    object-fit: cover;
    width: 100%;
}

/*Responsive*/

@media(max-width:812px){
    .mar{
        height: 120px;
     }
     .sponser-box h1{
 font-size: 20px;
     }
    .box-sp{
        height:80px ;
        width: 100px;
        margin: 0 30px;
    }
    .box-sp img{
        aspect-ratio: 3/2;
        object-fit: cover;
        width: 100%;
    }
    
}
@media(max-width:425px){
    .mar{
        height: 80px;
     }
     .sponser-box h1{
        font-size: 18px;
    }
    .box-sp{
        height:70px ;
        width: 80px;
        margin: 0 20px;
    }
    .box-sp img{
        aspect-ratio: 3/2;
        object-fit: cover;
        width: 100%;
    }
}