.main-item{
    /* display: grid;
    grid-template-columns: repeat(6,1fr); */
    /* justify-content: center;
     align-items: center; */
     /* width: 100%;
     background-color: rgb(243, 157, 157); */
    /* display: flex;
    flex-wrap: wrap; */

}
.item{
    box-shadow: rgba(131, 131, 131, 0.411) 0px 3px 8px;
    /* border: 1px solid rgb(165, 165, 165); */
    padding: 4px;
    border-radius: 10px;
    height: 250px;
    width: 200px;
    transform: translateY(5px);
    transition: all 0.5s ease;
    
}
.item img{
    height: 100%;
    width: 100%;
    border-radius: 10px;
    cursor: pointer;
}
.item:hover{
    transform: translateY(-5px);
    transition: all 0.5s ease;
}


/*Responsive design*/
@media(max-width:1313px){
    .item{
        height: 220px;
        width: 180px; 
    }
    .item img{
        height: 100%;
        width: 100%;
    }
}
